import React from "react";
import NavbarLinks from "./NavbarLinks";

function Navbar() {
  return (
    <div className="Navbar">
      <div className="Navbar-Logo">
        <img
          src="data:image/png;base64,"
          alt="logo"
          srcSet=""
        />
      </div>
      <NavbarLinks />
    </div>
  );
}

export default Navbar;
